// src/data/sampleListings.ts

const img = (i: number) => `https://picsum.photos/seed/alt${i}/1200/800`;

export type Listing = {
  id: string;
  name: string;
  category: "Venue" | "Decorator" | "Caterer" | "DJ" | "Photographer";
  subtype: string;
  city: string;
  region: string;
  description: string;
  pricePerNight: number;
  pricePerUnit?: number;
  capacity?: number;
  images: string[];
  features: string[];
  amenities: string[];

  // NEW fields for popularity + rating
  bookingsCount: number;
  popularity_score: number;
  avg_rating: number;
  reviewsCount: number;
};

const REGIONS: Record<string, string[]> = {
  Mumbai: ["Bandra", "Andheri", "Colaba", "Powai"],
  Delhi: ["GK", "Vasant Kunj", "Dwarka", "Hauz Khas"],
  Bengaluru: ["Indiranagar", "Koramangala", "Whitefield"],
  Chennai: ["T Nagar", "Velachery", "Anna Nagar"],
  Goa: ["Baga", "Candolim", "Anjuna"],
  Pune: ["Koregaon Park", "Baner", "Viman Nagar"],
};

const AMENITIES = [
  "Parking",
  "Power Backup",
  "Indoor",
  "Outdoor",
  "Sound System",
  "WiFi",
  "Air Conditioning",
  "Security",
];

const sample: Listing[] = [];

function randomRegion(city: string) {
  const arr = REGIONS[city] || ["Central"];
  return arr[Math.floor(Math.random() * arr.length)];
}

function random(min: number, max: number) {
  return Math.floor(Math.random() * (max - min)) + min;
}

function pushListing(
  i: number,
  category: Listing["category"],
  subtype: string,
  city: string
) {
  sample.push({
    id: `${category.toLowerCase()}-${i}`,
    name: `${subtype} ${category} ${i}`,
    category,
    subtype,
    city,
    region: randomRegion(city),
    description: `A beautiful ${subtype.toLowerCase()} perfect for private events and gatherings. Well-maintained, secure and with great service.`,
    pricePerNight: 15000 + (i * 200) % 8000,
    capacity: 50 + (i * 5) % 150,
    images: [img(i * 3 + 1), img(i * 3 + 2), img(i * 3 + 3)],
    features: ["Indoor", "Outdoor", "Parking", "Security"],

    // take 4 random amenities
    amenities: AMENITIES.sort(() => 0.5 - Math.random()).slice(0, 4),

    // popularity / ratings
    bookingsCount: random(10, 500),
    popularity_score: random(10, 500),
    avg_rating: parseFloat((Math.random() * 2 + 3).toFixed(1)), // 3.0 – 5.0
    reviewsCount: random(3, 200),
  });
}

const subtypesVenue = ["Farmhouse", "Villa", "Penthouse", "Studio", "Apartment"];
const subtypesDecorator = ["Floral Decor", "Luxury Decor", "Minimal Decor", "Boho Decor", "Theme Decor"];
const subtypesCaterer = ["Indian Cuisine", "Italian", "Asian Fusion", "Continental", "Street Food"];
const subtypesDJ = ["House", "Hip-Hop", "EDM", "Bollywood", "Mixed"];
const subtypesPhotog = ["Candid", "Cinematic", "Drone", "Fashion", "Event"];

let id = 1;
const cities = ["Mumbai", "Delhi", "Bengaluru", "Chennai", "Goa", "Pune"];

for (let i = 0; i < 6; i++) {
  pushListing(id++, "Venue", subtypesVenue[i % subtypesVenue.length], cities[i]);
  pushListing(id++, "Decorator", subtypesDecorator[i % subtypesDecorator.length], cities[i]);
  pushListing(id++, "Caterer", subtypesCaterer[i % subtypesCaterer.length], cities[i]);
  pushListing(id++, "DJ", subtypesDJ[i % subtypesDJ.length], cities[i]);
  pushListing(id++, "Photographer", subtypesPhotog[i % subtypesPhotog.length], cities[i]);
}

// Add extra venues to reach ~30
for (let j = 0; j < 5; j++) {
  pushListing(id++, "Venue", subtypesVenue[j % subtypesVenue.length], cities[j]);
}

export default sample;
